@import '../base.scss';

.c-before-and-after-slider {
  @include t-v-gutters();

  &__inner {
    @include t-gutters(true);
    position: relative;
  }

  &__blue-background {
    display: none;
  }

  &__pre-title {
    text-align: center;
    color: $s-color--font-1;
  }

  &__title {
    text-align: center;
    color: $s-color--font-1;
    margin: t-to-rem(0) t-to-rem(50) t-to-rem(24);
  }

  &__slides {
    display: flex;
    overflow-x: auto;

    &.keen-slider {
      overflow: hidden;
    }
  }

  &__slide {
    display: block;
    flex-shrink: 0;
    width: 100%;
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: center;
    color: $s-color--font-5;
    margin-bottom: t-to-rem(28);

    .c-before-and-after-slider__right-container & {
      display: none;
    }

    &-text {
      color: $s-color--font-8;
    }
  }

  &__images {
    display: flex;
    justify-content: center;
    margin: 0 auto t-to-rem(28);
    max-width: t-to-rem(400);
  }

  &__image {
    @include t-absolute-full;
    object-fit: cover;
    height: 100%;

    &-group {
      flex: 1;
      flex-shrink: 0;

      &.is-before {
        margin-right: t-to-rem(18);
      }
    }

    &-container {
      padding-bottom: 180%;
      position: relative;
    }

    &-caption {
      margin: t-to-rem(8) 0 0 t-to-rem(4);
      text-align: center;
    }
  }

  &__right-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: t-to-rem(350);
    margin: 0 auto;
  }

  &__arrows {
    display: flex;
    justify-content: space-between;
    width: t-to-rem(100);
    margin-bottom: t-to-rem(30);
  }

  &__arrow {
    width: t-to-rem(42);
    height: t-to-rem(42);
    background: $s-color--background-octonary url('../../assets/icon-arrow-right-w.svg') no-repeat center;
    border-radius: 100%;
    display: block;
    background-size: t-to-rem(13);
    cursor: pointer;

    &.is-left {
      transform: rotate(180deg);
    }
  }

  &__slide-title {
    @include t-font--heading-5;
    color: $s-color--font-1;
    margin-bottom: t-to-rem(18);
    text-transform: capitalize;
  }

  &__slide-text {
    color: $s-color--font-5;
    margin-bottom: t-to-rem(28);
  }

  &__related-products {
    display: flex;
    justify-content: center;

    &-image {
      margin-bottom: t-to-rem(10);
    }
  }

  &__cta {
    &-container {
      text-align: center;
      margin-top: 2rem;
    }
  }

  .o-related-product {
    display: block;

    &.is-disabled {
      filter: brightness(0.8);
      cursor: default;

      .o-related-product {
        &__image-icon {
          pointer-events: none;
        }
      }
    }

    &:nth-of-type(2) {
      margin: t-to-rem(24) 0 0 t-to-rem(32);
    }

    &__image {
      &-container {
        background-color: $s-color--background-octonary;
        height: t-to-rem(126);
        width: t-to-rem(92);
        position: relative;
        margin-bottom: t-to-rem(10);
        line-height: 1.3;

        &.is-before {
          margin-right: 2.5%;
        }
      }

      height: 75%;
      width: auto;
      @include t-absolute-center-x-y;

      &-icon {
        background: white;
        border-radius: 100%;
        @include t-absolute-center-x-y;
        width: t-to-rem(29);
        height: t-to-rem(29);
        transition: all $s-anim--time-med;
        cursor: pointer;

        &:hover {
          background-color: $s-color--font-1;

          svg path {
            fill: white;
          }
        }

        svg {
          @include t-absolute-center-x-y;
          @include t-width-and-height(10);
          transition: all $s-anim--time-med;
        }
      }
    }

    &__details {
      color: $s-color--font-1;
    }
  }

  @include t-media-query($s-small-up) {

    &__title {
      margin-bottom: t-to-rem(49);
    }
  }

  @include t-media-query($s-medium-up) {

    &__blue-background {
      display: block;
      background-color: $s-color--background-octonary;
      width: 38.7%;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
    }

    &__inner {
      padding-top: t-to-rem(66);
      padding-bottom: t-to-rem(66);
    }

    &__slide {
      display: flex;
    }

    &__cta {
      &-container {
        margin-top: 0;
      }
    }

    &__related-products {
      justify-content: left;
    }

    &__images {
      width: 42.44%;
      margin-right: 8.1%;
      flex-shrink: 0;
      max-width: unset;
      margin-bottom: unset;
    }

    &__image {
      &-container {
        padding-bottom: 170%;
      }

      &-caption {
        margin-top: t-to-rem(24);
        text-align: unset;
      }
    }

    &__image-container.is-before {
      margin-right: 2.47%;
    }

    &__right-container {
      width: 40%;
      align-items: unset;
      text-align: unset;
      max-width: unset;
    }

    &__arrows {
      width: t-to-rem(118);
    }

    &__arrow {
      width: t-to-rem(50);
      height: t-to-rem(50);
      background-size: t-to-rem(16);

      &:hover {
        background-color: $s-color--background-quinary;
      }
    }

    &__info {
      display: none;
      justify-content: unset;

      .c-before-and-after-slider__right-container & {
        display: flex;
      }
    }

    &__title {
      margin-bottom: t-to-rem(30);
    }

    .o-related-product {

      &__image {
        &-container {
          margin-right: t-to-rem(22);
        }
      }
    }

    .o-related-product {
      display: flex;
      align-items: center;

      &:nth-of-type(1) {
        margin-right: t-to-rem(32);
      }

      &:nth-of-type(2) {
        margin: unset;
      }

      &__image {
        &-container {
          height: t-to-rem(131);
          width: t-to-rem(95);
        }
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding-top: t-to-rem(76);
      padding-bottom: t-to-rem(76);
    }

    &__title {
      font-size: t-to-rem(72);
      margin-bottom: t-to-rem(40);
    }

    &__right-container {
      margin-top: t-to-rem(44);
    }

    &__arrows {
      margin-bottom: t-to-rem(40);
    }

    &__slide {
      &-title {
        margin-bottom: t-to-rem(36);
      }

      &-text {
        margin-bottom: t-to-rem(50);
      }
    }

    .o-related-product {
      &:nth-of-type(1) {
        margin-right: t-to-rem(54);
      }
    }
  }
}